/* App.css */

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f8f9fa;
  margin: 0;
  padding: 0;
}

.app-container {
  padding: 1rem;
  max-width: 1200px;
  margin: auto;
}

.title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #20232a;
}

.search-bar {
  text-align: center;
  margin-bottom: 1.5rem;
}

.search-bar input {
  width: 90%;
  max-width: 400px;
  padding: 10px 14px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 6px;
  outline: none;
}

.table-wrapper {
  overflow-x: auto;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.styled-table {
  width: 100%;
  border-collapse: collapse;
  background-color: #ffffff;
  min-width: 800px;
}

.styled-table thead tr {
  background-color: #20232a;
  color: #ffffff;
  text-align: left;
}

.styled-table th,
.styled-table td {
  padding: 10px 14px;
  border-bottom: 1px solid #ddd;
  white-space: nowrap;
  text-align: center;
  font-size: 0.9rem;
}

.styled-table th {
  cursor: pointer;
  position: sticky;
  top: 0;
  z-index: 1;
}

.styled-table tbody tr:hover {
  background-color: #f1f1f1;
}

/* MSR highlight classes */
.highlight-high {
  background-color: #c1f2c7; /* green */
}

.highlight-mid {
  background-color: #ffeaa7; /* yellow */
}

.highlight-low {
  background-color: #ffcccc; /* red */
}

.msr-bar {
  background-color: #2e86de;
  height: 100%;
  padding: 4px 8px;
  color: white;
  font-weight: bold;
  border-radius: 4px;
  display: inline-block;
  min-width: 30px;
}

/* ðŸ“± Mobile responsiveness */
@media (max-width: 768px) {
  .title {
    font-size: 1.4rem;
  }

  .search-bar input {
    width: 95%;
    font-size: 0.95rem;
  }

  .styled-table th,
  .styled-table td {
    padding: 8px 10px;
    font-size: 0.85rem;
  }

  .msr-bar {
    font-size: 0.85rem;
    padding: 3px 6px;
  }
}

@media (max-width: 480px) {
  .title {
    font-size: 1.2rem;
  }

  .search-bar input {
    font-size: 0.9rem;
  }

  .styled-table {
    font-size: 0.8rem;
  }
}








